.line {
    width: 1065px;
    height: 0.1px;
    margin-left: 0px;
    margin-top: -5px;
    background: #EEEFF3;
}

header {
    width: 1287.763px;
    height: 158.217px;
}


.details {
    display: flex;
    flex-direction: row;
    justify-content: right;
}

.details div {
    margin: 20px;
}


#text p {
    margin-top: 7px;
    margin-left: -10px;
    color: #959CB6;
    font-family: Poppins;
    font-size: 11px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

#alpha {
    width: 43.368px;
    height: 42.117px;
    border-radius: 4px;
    background: #D7F9EF;
    margin-top: 10px;
}

#alpha p {
    margin-top: 12px;
    margin-left: 15px;
    color: #0BB783;
    font-family: Poppins;
    font-size: 17px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.seclayer {
    display: flex;
    justify-content: space-between;
    margin-right: 10px;
    padding-top: 20px;
}

.seclayer div {
    /* margin-right: 30px; */
}

.seclayer div p {
    margin-top: 8px;
    color: #959CB6;
    margin-left: 20px;
    font-family: Poppins;
    font-size: 13px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

#seclayerleft {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

#seclayerright {
    width: 500px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

#today {
    width: 71.075px;
    height: 33.693px;
    border-radius: 4px;
    background: #F3F6F9;
}

#actions {
    width: 93.964px;
    height: 38.507px;
    border-radius: 4px;
    background: #F3F6F9;
}

.blocks {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    background: #EEF0F8;
    padding-top: 20px;
    padding-left: 10px;
}

.blocks div {
    display: flex;
    width: 104px;
    height: 127px;
    padding: 48px 33px 48px 28px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    border: 1px solid #EFF2F5;
    background: #FFF;
}

.blocks div img {
    width: 48px;
    height: 48px;
}

#movie {
    background: #50CD89;
}

.blocks .sideDiv {
    border: none;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 407px;
    height: 675px;
    justify-content: space-around;
    padding: 0px;
    background: #EEF0F8;
}

.blocks .sideDiv div {
    margin-top: -20px;
    width: 120px;
    height: 107.407px;
    border-radius: 12px;
    background: #FFF;
    box-shadow: 0px 0px 20px 0px rgba(94, 98, 120, 0.04);
}

.thirdLayer {
    width: 850px;
    height: 410px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: -420px;
}

.thirdLayer div {
    width: 400px;
    height: 380px;
    border-radius: 12px;
    margin-top: 20px;
    background: #FFF;
}

#innerDiv {
    margin-top: 0px;
    width: 400px;
    height: 242px;
    border-radius: 12px;
    background: #F64E60;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

#categories {
    color: #FFF;
    font-family: Poppins;
    font-size: 17px;
    font-style: normal;
    font-weight: 600;
    line-height: 21px;
}

#export {
    width: 85px;
    height: 30px;
    margin-right: 30px;
    margin-top: 30px;
    flex-shrink: 0;
    border-radius: 6px;
/* opacity: 0.12; */
background: #efbfbf;
}

#export p {
    margin-top: 7px;
    margin-left: 20px;
    color: white;
font-family: Poppins;
font-size: 11px;
font-style: normal;
font-weight: 600;
line-height: normal;
}